> section {\n    display: flex;\n    flex-direction: column;\n\n    & > img {\n      width: 100%;\n    }\n\n    h1 {\n      font-size: 1.8em;\n      color: darken($light, 45%);\n      margin: 0 auto;\n    }\n\n    h2,\n    h4 {\n      font-size: 1.3em;\n      color: darken($light, 15%);\n      margin: 0 auto;\n      padding: 0;\n    }\n\n    i.indicator {\n      color: darken($light, 10%);\n      font-size: 1.7em;\n      border-radius: 50%;\n      padding: 0.4em;\n      border: 2px solid darken($light, 10%);\n      margin: 0 auto;\n      align-self: center;\n    }\n  }\n\n  & > footer {\n    display: flex;\n    flex-flow: row nowrap;\n\n    button {\n      height: 45px;\n      flex: 1;\n\n      .flex-wrapper {\n        justify-content: center !important;\n      }\n\n      &[theme='primary'] {\n        .flex-wrapper {\n          justify-content: center !important;\n        }\n      }\n    }\n  }\n\n  &[type='success'] {\n    & > section {\n      i {\n        color: $success;\n        border-color: $success;\n      }\n    }\n\n    button[theme='primary'] {\n      width: 100%;\n\n      .flex-wrapper {\n        justify-content: center;\n      }\n    }\n  }\n\n  &[type='warning'] {\n    footer {\n      display: flex;\n\n      .flex-wrapper {\n        justify-content: center;\n      }\n    }\n\n    button {\n      width: 50%;\n      justify-content: center;\n\n      &:first-of-type {\n        margin: 0 0.5% 0 0;\n      }\n\n      &:last-of-type {\n        margin: 0 0 0 0.5%;\n      }\n    }\n  }\n\n  &[type='error'] {\n    button[theme='primary'].primary {\n      width: 100%;\n\n      .flex-wrapper {\n        justify-content: center;\n      }\n    }\n  }\n}\n\nnovo-notification {\n  & > section {\n    padding: 0 20px;\n    margin: 15px 0 55px;\n\n    h1 {\n      margin: 10px auto 0;\n    }\n  }\n}\n","novo-card {\n  .novo-card {\n    background-color: $white;\n    box-shadow: $whiteframe-shadow-z3;\n    border-radius: 0.4em;\n    position: relative;\n    overflow-x: hidde