 0;\n      }\n    }\n    footer {\n      display: flex;\n      justify-content: center;\n    }\n  }\n}\n",".calendar-month-view {\n  background-color: $white;\n\n  .calendar-header {\n    display: flex;\n    flex-flow: column;\n    text-align: center;\n    font-weight: bolder;\n    border-bottom: 2px solid #e1e1e1;\n    .calendar-header-top {\n      display: flex;\n      flex-flow: row nowrap;\n      align-items: center;\n      justify-content: space-around;\n\n      .calendar-month {\n        font-size: 180%;\n      }\n      .calendar-year {\n        color: #999;\n      }\n    }\n    .calendar-weekdays {\n      display: flex;\n      flex-flow: row nowrap;\n      .calendar-weekday {\n        padding: 5px 0;\n        overflow: hidden;\n        text-overflow: ellipsis;\n        display: block;\n        white-space: nowrap;\n        flex: 1;\n      }\n    }\n  }\n\n  .calendar-cell-row {\n    display: flex;\n    &:hover {\n      background-color: #fafafa;\n    }\n  }\n\n  .calendar-cell-row .calendar-cell:hover,\n  .calendar-cell.calendar-has-events.calendar-open {\n    background-color: #ededed;\n  }\n\n  .calendar-days {\n    border: 1px solid #e1e1e1;\n    border-bottom: 0;\n  }\n\n  .calendar-day-top {\n    display: flex;\n    flex-flow: row nowrap;\n    align-items: center;\n    justify-content: flex-end;\n  }\n  .calendar-cell:hover {\n    .calendar-actions {\n      display: flex;\n    }\n  }\n\n  .calendar-cell {\n    float: left;\n    flex: 1;\n    display: flex;\n    flex-direction: column;\n    align-items: stretch;\n    position: relative;\n  }\n\n  .calendar-day-cell {\n    min-height: 56px;\n\n    &:not(:last-child) {\n      border-right: 1px solid #e1e1e1;\n    }\n  }\n\n  .calendar-days .calendar-cell-row {\n    border-bottom: 1px solid #e1e1e1;\n  }\n\n  .calendar-day-badge {\n    background-color: #b94a48;\n    display: inline-block;\n    min-width: 10px;\n    padding: 3px 7px;\n    font-size: 12px;\n    font-weight: 700;\n    line-height: 1;\n    color: white;\n    text-align: center;\n    white-space: nowrap;\n    vertical-align: middle;\n    border-radius: 10px;\n  }\n\n  .calendar-day-number {\n    font-size: 1.2em;\n    font-weight: 400;\n    opacity: 0.5;\n    padding: 4px;\n  }\n\n  .calendar-event {\n    width: 22px;\n    height: 22px;\n    border-radius: 4px;\n    display: inline-block;\n    margin: 2px;\n    vertical-align: middle;\n    text-align: center;\n    line-height: 22px;\n    font-size: 12px;\n    color: $white;\n  }\n\n  .calendar-day-cell.calendar-in-month.calendar-has-events {\n    cursor: pointer;\n  }\n\n  .calendar-day-cell.calendar-out-month .calendar-day-number {\n    opacity: 0.1;\n    cursor: default;\n  }\n\n  .calendar-day-cell.calendar-weekend .calendar-day-number {\n    color: darkred;\n  }\n\n  .calendar-day-cell.calendar-today {\n    background-color: #e8fde7;\n  }\n\n  .calendar-day-cell.calendar-today .calendar-day-number {\n    color: $dark;\n  }\n\n  .calendar-day-cell.calendar-drag-over {\n    background-color: darken(#ededed, 5%) !important;\n  }\n\n  .calendar-open-day-events {\n    padding: 15px;\n    color: white;\n    background-color: #555;\n    box-shadow: inset 0 0 15px 0 rgba(0, 0, 0, 0.5);\n  }\n\n  .calendar-open-day-events .calendar-event {\n    position: relative;\n    top: 2px;\n  }\n\n  .calendar-event-title {\n    color: white;\n  }\n\n  .calendar-out-month {\n    .calendar-day-badge,\n    .calendar-event {\n      opacity: 0.3;\n    }\n  }\n}\n",".cal-week-view {\n  position: relative;\n\n  .cal-day-headers {\n    display: flex;\n    border: 1px solid #e1e1e1;\n  }\n\n  .cal-day-headers .cal-header {\n    flex: 1;\n    text-align: center;\n    padding: 5px;\n    background-color: $off-white;\n  }\n\n  .cal-day-headers .cal-header:not(:last-child) {\n    border-right: 1px solid #e1e1e1;\n  }\n\n  .cal-day-headers .cal-header:hover,\n  .cal-day-headers .cal-drag-over {\n    background-color: #ededed;\n  }\n\n  .cal-day-headers span {\n    font-weight: 400;\n  