n        background-color: $positive;\n        color: #fff;\n      }\n      &:hover {\n        cursor: pointer;\n        background-color: $positive;\n        color: #fff;\n      }\n    }\n    .day {\n      height: 30px;\n      width: 30px;\n      line-height: 27px;\n      margin: 1px;\n      border: none;\n      background-color: transparent;\n      border-radius: 50%;\n      box-shadow: inset 0 0 0 2px transparent;\n      transition: box-shadow 140ms ease-in-out;\n      position: relative;\n      &:focus {\n        outline: none;\n      }\n      &:disabled {\n        color: #d7d9e4;\n        cursor: not-allowed !important;\n        box-shadow: none !important;\n      }\n    }\n    td {\n      &.notinmonth {\n        color: #d7d9e4;\n      }\n      &:hover {\n        .day {\n          cursor: pointer;\n          box-shadow: inset 0 0 0 2px $positive;\n        }\n      }\n      &.filler button {\n        background: $positive;\n        color: #fff;\n        height: 30px;\n        width: 100%;\n        border-radius: 0;\n      }\n      &.selecting-range button {\n        position: relative;\n        &:before {\n          content: '';\n          background: rgba($positive, 0.2);\n          position: absolute;\n          z-index: -1;\n          width: 22px;\n          height: 22px;\n          top: 50%;\n          left: 50%;\n          transform: translate(-50%, -50%);\n          border-radius: 50%;\n        }\n      }\n      &.filler + .selected button,\n      &.startfill + .selected button,\n      &.startfill button {\n        border-top-left-radius: 0;\n        border-bottom-left-radius: 0;\n        .day {\n          box-shadow: 0;\n        }\n      }\n      &.startfill button {\n        border-radius: 50%;\n        border-top-right-radius: 0;\n        border-bottom-right-radius: 0;\n        box-shadow: none !important;\n        position: relative;\n        &:before {\n          content: '';\n          position: absolute;\n          height: 100%;\n          background: #4a89dc;\n          width: 10px;\n          top: 0;\n          right: -5px;\n          z-index: -1;\n        }\n      }\n      &.endfill button {\n        border-radius: 50%;\n        border-top-left-radius: 0;\n        border-bottom-left-radius: 0;\n        box-shadow: none !important;\n        position: relative;\n        &:before {\n          content: '';\n          position: absolute;\n          height: 100%;\n          background: #4a89dc;\n          width: 10px;\n          top: 0;\n          left: -5px;\n          z-index: -1;\n        }\n      }\n      &.selected {\n        .day {\n          background: $positive;\n          color: #fff;\n        }\n      }\n      &.today {\n        .day:after {\n          content: '';\n          position: absolute;\n          top: 0;\n          left: 50%;\n          transform: translateX(-15px);\n          border-radius: 100%;\n          width: 100%;\n          height: 100%;\n          max-width: 30px;\n          margin: 0 auto;\n          box-shadow: inset 0 0 0 2px $light;\n        }\n        &.filler,\n        &.selected {\n          .day:after {\n            box-shadow: inset 0 0 0 2px #9dbeff;\n          }\n        }\n      }\n    }\n  }\n  .calendar-footer {\n    width: 100%;\n    padding: 15px 10px;\n    text-align: left;\n    .today {\n      cursor: pointer;\n      color: $positive;\n    }\n  }\n}\n\n@keyframes select {\n  from {\n    opacity: 0.8;\n    transform: scale(1);\n  }\n  to {\n    opacity: 0;\n    transform: scale(1.6);\n  }\n}\n","novo-time-picker-input {\n  flex: 1;\n  position: relative;\n  display: block;\n  max-width: 130px;\n\n  &.disabled {\n    pointer-events: none;\n    opacity: 1;\n  }\n\n  input {\n    font-size: 1em;\n    border: none;\n    border-bottom: 1px solid rgba(lighten($dark, 12%), 0.25);\n    background: transparent !important;\n    border-radius: 0;\n    outline: none;\n    height: 2rem;\n    width: 100%;\n    margin: 0;\n    padding: 0;\n    box-shadow: none;\n    box-sizing: con