        line-height: 19px;\n      }\n      &:focus,\n      &:hover {\n        background: lighten($light, 10%);\n        color: darken($light, 55%);\n      }\n      &.active {\n        // border: 1px solid $positive;\n        color: $positive;\n        font-weight: 500;\n      }\n    }\n  }\n\n  /* ANALOG CLOCK\n    ----------------------------- */\n  .analog {\n    height: 250px;\n    width: 250px;\n    position: relative;\n    margin: 10% auto;\n  }\n  .analog--inner {\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    width: 100%;\n    position: absolute;\n    transition: transform 125ms linear;\n  }\n  .analog--face {\n    top: 5px;\n    right: 5px;\n    left: 5px;\n    bottom: 5px;\n    position: absolute;\n    border-radius: 50%;\n  }\n  .analog--hand--hours {\n    width: 240px;\n    height: 240px;\n    display: block;\n    position: absolute;\n    top: 0;\n    border-radius: 50%;\n    transform: rotate(0deg);\n    transition: transform 100ms linear;\n    z-index: 1;\n    &:before {\n      content: ' ';\n      width: 2px;\n      top: 0;\n      bottom: 0;\n      left: 30%;\n      margin: 30%;\n      z-index: -1;\n      display: block;\n      position: absolute;\n      transform: rotate(90deg);\n    }\n    .analog--ball {\n      height: 37px;\n      width: 37px;\n      display: block;\n      right: 8px;\n      top: 50%;\n      margin-top: -18px;\n      border-radius: 50%;\n      position: absolute;\n      border: 3px solid $positive;\n      background: $positive;\n    }\n    &.hour-12 {\n      transform: rotate(-90deg);\n    }\n    &.hour-1 {\n      transform: rotate(-60deg);\n    }\n    &.hour-2 {\n      transform: rotate(-30deg);\n    }\n    &.hour-3 {\n      transform: rotate(0);\n    }\n    &.hour-4 {\n      transform: rotate(30deg);\n    }\n    &.hour-5 {\n      transform: rotate(60deg);\n    }\n    &.hour-6 {\n      transform: rotate(90deg);\n    }\n    &.hour-7 {\n      transform: rotate(120deg);\n    }\n    &.hour-8 {\n      transform: rotate(150deg);\n    }\n    &.hour-9 {\n      transform: rotate(180deg);\n    }\n    &.hour-10 {\n      transform: rotate(210deg);\n    }\n    &.hour-11 {\n      transform: rotate(240deg);\n    }\n  }\n  .analog--hand--minutes {\n    width: 200px;\n    height: 200px;\n    margin: 20px;\n    display: block;\n    position: absolute;\n    top: 0;\n    border-radius: 50%;\n    transform: rotate(0deg);\n    transition: transform 100ms linear;\n    z-index: 1;\n    &:before {\n      left: 23%;\n      margin: 33%;\n      content: ' ';\n      width: 2px;\n      top: 0;\n      bottom: 0;\n      z-index: -1;\n      display: block;\n      position: absolute;\n      transform: rotate(90deg);\n    }\n    .analog--ball {\n      display: block;\n      top: 50%;\n      border-radius: 50%;\n      position: absolute;\n      height: 32px;\n      width: 32px;\n      right: 25px;\n      margin-top: -16px;\n      border: 2px solid lighten($positive, 10%);\n      background: lighten($positive, 10%);\n      transition: all 160ms ease-in-out;\n      &.between {\n        height: 1px;\n        border-radius: 2em;\n        margin-top: 0;\n      }\n    }\n    &.min-00 {\n      transform: rotate(-90deg);\n    }\n    @for $i from 1 through 9 {\n      $deg: ($i * (30/5) - 90);\n      &.min-0#{$i} {\n        transform: rotate(#{$deg}deg);\n      }\n    }\n    @for $i from 10 through 59 {\n      $deg: ($i * (30/5) - 90);\n      &.min-#{$i} {\n        transform: rotate(#{$deg}deg);\n      }\n    }\n  }\n  .analog--center {\n    height: 155px;\n    width: 155px;\n    border-radius: 50%;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n    z-index: 1;\n    margin-top: 1px;\n    margin-left: 1px;\n    display: block;\n    position: absolute;\n    background-color: $background;\n  }\n  .analog--hour,\n  .analog--minute {\n    font-size: 1.6em;\n    color: #666666;\n    left: 50%;\n    top: 50%;\n    z-index: 3;\n    text-align: center;\n    width: 40px;\n    padding: 8px 0;\n    border-radius: 50%;\n    cursor: pointer;\n    margin-left: -20px;\n    margin-top: -20px;\n    position: absolute;\n    &.active {\n      color: #fff;\n    }\n  }\n  .analog--minute {\n    font-size: 1em;\n    margin-left: -20px;\n    margin-top: -16px;\n  }\n  .analog--hours,\n  .analog--minutes {\n    width: 250px;\n    height: 250px;\n    float: left;\n    position: relative;\n  }\n  .analog--minutes {\n    position: absolute;\n  }\n  $d: 4.5em;\n  @for $i from 1 through 12 {\n    $deg: ($i * 30 - 90);\n    .analog--hour:nth-child(#{$i}) {\n      transform: rotate(#{$deg}deg) translate($d) rotate(#{$deg * -1}deg);\n    }\n    .analog--minute:nth-child(#{$i}) {\n      transform: rotate(#{$deg}deg) translate($d) rotate(#{$deg * -1}deg);\n    }\n  }\n  &.military {\n    $md: 8em;\n    @for $i from 1 through 24 {\n      $hour: $i - 1;\n      $deg: ($i * 15 - 105);\n      .analog--hour:nth-child(#{$i}) {\n        transform: rotate(#{$deg}deg) translate($md) rotate(#{$deg * -1}deg);\n      }\n      .analog--hand--hours {\n        top: -10px;\n        left: -10px;\n        &.hour-#{$hour} {\n          transform: rotate(#{$deg}deg);\n        }\n      }\n    }\n    .analog--face {\n      top: 15px;\n      right: 15px;\n      left: 15px;\n      bottom: 15px;\n    }\n    .analog--hour {\n      font-size: 0.9em;\n      margin-left: -20px;\n      margin-top: -16px;\n    }\n    .analog--hand--minutes {\n      margin: 10px;\n    }\n    .analog--hand--hours {\n      .analog--ball {\n        height: 28px;\n        width: 28px;\n        right: 13px;\n        margin-top: -14px;\n      }\n    }\n  }\n}\n","$time-select-height: 45px;\n\nnovo-date-time-picker-input {\n  flex: 1;\n  position: relative;\n  display: flex;\n  flex-flow: row nowrap;\n  max-width: 340px;\n\n  novo-date-picker-input {\n    margin-right: 20px;\n  }\n  novo-time-picker-input {\n    max-width: 130px;\n  }\n}\n\nnovo-date-time-picker {\n  display: block;\n  width: 250px;\n  min-width: 250px;\n  overflow: hidden;\n  bord