x-height: 250px;\n        margin-top: auto;\n        margin-bottom: auto;\n      }\n    }\n  }\n  section {\n    &.design {\n      background: $white;\n    }\n    > aside {\n      background: $background;\n      padding: 10px 20px;\n      border-radius: 3px;\n      display: inline-block;\n      border: 2px solid darken($background, 2%);\n      h6 {\n        color: $mint;\n        margin-bottom: 5px;\n      }\n      &.design-principle {\n        h6 {\n          border-bottom: 1px solid darken($background, 5%);\n          padding-bottom: 8px;\n          span {\n            font-weight: 300;\n          }\n        }\n        .hint {\n          display: block;\n          color: darken($background, 50%);\n          margin: 5px 0;\n          padding: 10px;\n          font-size: 0.9em;\n          border-radius: 3px;\n          background: darken($background, 5%);\n          border: 1px solid darken($background, 10%);\n          i {\n            margin-right: 10px;\n          }\n          > span {\n            display: block;\n          }\n          .link {\n            display: block;\n            margin-top: 10px;\n            font-weight: 400;\n            strong {\n              font-weight: 600;\n            }\n          }\n        }\n      }\n    }\n  }\n  .accepted,\n  .not-accepted {\n    display: block;\n    margin-top: 15px;\n  }\n  .not-accepted {\n    color: $negative;\n  }\n  .accepted {\n    color: $success;\n  } // Custom page element initial display reset\n  color,\n  home,\n  iconography,\n  layout,\n  typography {\n    display: block;\n    & > header {\n      background: $off-white;\n    }\n    & > section.design.container {\n      article {\n        margin: 80px 0;\n      }\n    }\n  } // DEMO STYLES\n  // Custom page element initial display reset\n  buttons-demo,\n  from-demo,\n  modal-demo,\n  switch-demo,\n  tooltip-demo {\n    display: block;\n  }\n}\n\nmulti-code-snippet {\n  background: #f8f8f8;\n  border: 1px solid #e9eaea;\n  display: block;\n  lab