igin: 50% 50%;\n      animation-duration: 20000ms;\n      animation-iteration-count: infinite;\n      animation-timing-function: linear;\n      animation-name: rotate;\n    }\n  }\n\n  // Bullhorn SVG\n  [data-name='bullhorn-text'] {\n    width: 100%;\n    max-width: 150px;\n    margin: 0 20px;\n    display: inline-block;\n  }\n\n  // Start menu link styles\n  .menu-list {\n    position: relative;\n    margin: 20px 0 0;\n    padding: 0;\n    list-style: none;\n    padding: 20px 20px 60px;\n  }\n\n  .menu-item {\n    display: block;\n    margin: 0;\n    height: 2.5rem;\n  }\n\n  .menu-link {\n    display: block;\n    width: 90%;\n    height: 2.5rem;\n    line-height: 2.5rem;\n    text-align: left;\n    color: darken(#acaeb1, 4%);\n    transition: color 0.3s;\n    font-size: 1.05em;\n    font-weight: 400;\n    cursor: pointer;\n    user-select: none;\n    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);\n    padding-left: 15px;\n  }\n\n  .menu-link:focus,\n  .menu-link:hover,\n  a.menu-item-header:focus,\n  a.menu-item-header:hover {\n    outline: none;\n    color: #fff;\n  }\n\n  .router-link-active.menu-link,\n  .router-link-active.menu-item-header {\n    color: #fff;\n    transition: color 0.5s;\n  }\n\n  .menu-section-header {\n    display: block;\n\n    .menu-item-header {\n      color: darken(#acaeb1, 4%);\n      height: 2.5rem;\n      line-height: 2.5rem;\n      display: block;\n      text-transform: uppercase;\n      font-size: 0.9em;\n      font-weight: 500;\n      letter-spacing: 0.3px;\n      opacity: 0.25;\n    }\n  }\n\n  .menu-line {\n    position: absolute;\n    left: 90%;\n    top: 20px;\n    height: 2.5rem;\n    width: 4px;\n    pointer-events: none;\n    background: $bittersweet;\n    transition: transform 0.5s;\n    transition-timing-function: cubic-bezier(1, 0.01, 0, 1.22);\n  }\n\n  @for $i from 1 through 45 {\n    $number: ($i - 1);\n\n    .menu-item:nth-child(#{$i}).current ~ .menu-line {\n      transform: translate3d(0, percentage($number), 0);\n